"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";

export default function SignupForm() {
  const router = useRouter();

  const [formData, setFormData] = useState({
    name: "",
    email: "",
    phone: "",
  });

  const [errors, setErrors] = useState({});

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const validate = () => {
    const newErrors = {};

    if (!formData.name.trim()) {
      newErrors.name = "Name is required";
    }

    if (!formData.phone.trim()) {
      newErrors.phone = "Phone number is required";
    }

    return newErrors;
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const validationErrors = validate();

    if (Object.keys(validationErrors).length > 0) {
      setErrors(validationErrors);
      return;
    }

    setErrors({});
    console.log("Signup data:", formData);
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-50 to-slate-100">
      <form
        onSubmit={handleSubmit}
        className="w-full max-w-sm bg-white p-8 rounded-2xl shadow-sm border border-slate-200"
      >
        <div className="mb-8">
          <h1 className="text-3xl font-semibold text-slate-900 text-center mb-2">
            Create Account
          </h1>
          <p className="text-center text-slate-500 text-sm">
            Join us and get started today
          </p>
        </div>

        {/* Name */}
        <div className="mb-5">
          <input
            type="text"
            name="name"
            placeholder="Full Name"
            value={formData.name}
            onChange={handleChange}
            className={`w-full px-4 py-2.5 text-slate-900 placeholder-slate-400 border rounded-lg transition-all focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
              errors.name ? "border-red-300 bg-red-50" : "border-slate-200 bg-slate-50"
            }`}
          />
          {errors.name && (
            <p className="text-red-500 text-xs mt-1.5">{errors.name}</p>
          )}
        </div>

        {/* Email */}
        <div className="mb-5">
          <input
            type="email"
            name="email"
            placeholder="Email (optional)"
            value={formData.email}
            onChange={handleChange}
            className="w-full px-4 py-2.5 text-slate-900 placeholder-slate-400 border border-slate-200 bg-slate-50 rounded-lg transition-all focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
          />
        </div>

        {/* Phone */}
        <div className="mb-6">
          <input
            type="tel"
            name="phone"
            placeholder="Phone Number"
            value={formData.phone}
            onChange={handleChange}
            className={`w-full px-4 py-2.5 text-slate-900 placeholder-slate-400 border rounded-lg transition-all focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
              errors.phone ? "border-red-300 bg-red-50" : "border-slate-200 bg-slate-50"
            }`}
          />
          {errors.phone && (
            <p className="text-red-500 text-xs mt-1.5">{errors.phone}</p>
          )}
        </div>

        <button
          type="submit"
          className="w-full cursor-pointer bg-blue-600 hover:bg-blue-700 text-white py-2.5 rounded-lg font-medium transition-colors duration-200"
        >
          Sign Up
        </button>

        <p className="text-center text-sm text-slate-600 mt-6">
          Already have an account?
          <button
            type="button"
            onClick={() => router.push("/auth/login")}
            className="text-blue-600 cursor-pointer font-medium hover:text-blue-700 ml-1 transition-colors duration-200"
          >
            Login
          </button>
        </p>
      </form>
    </div>
  );
}
